@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
* {
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    /* ===== Colors ===== */
    --body-color: #E4E9F7;
    --sidebar-color: #FFF;
    --primary-color: #695CFE;
    --primary-color-light: #F6F5FF;
    --toggle-color: #DDD;
    --text-color: #707070;
}

body {
    height: 100vh;
    background: #FFF;
}


.container {
    display:flex;
}


html body #root {
    min-height: 100%;
}



.NewProjectButton p {
    margin:0;
    padding:0;
}

.NewProjectButton {
    background-color: #695CFE;
    font-size: 25px;
}
 .activity {
    display:flex;
    flex-wrap: wrap;
    margin-left: 7%;
    padding-bottom: 10px;
} 

.title {
    margin-left: 15%;
}

.footer {
    position: fixed;
    width: 100%;
    left: 94%;
    bottom: 5%;
}

 .card {
    margin: 20px;
    margin-bottom: 10px;
}  
.grid > div {
    flex-basis: calc(30% - 40px);
}

.buttonnn {
    align-items: center;
}

@media (max-width: 1000px) {
    .activity {
        padding-top: 60px;
    }

    .footer {
        left: 80%;
    }

}



.error {
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.p1 {
  font-size: 100px;
}

.p2 {
  font-size: 30px;
}


.Task {
    display:flex;
    flex-direction: column;
    align-items: center;
  }

.NewTaskButton-2  {
    background-color: #695CFE;
}
.NewTaskButton-2 p {
  margin:0;
  padding:0;
  font-size: 25px;
  font-weight: bold;
}

.startButton {
  margin-left: auto;
}

.card-title {
  font-size: 30px;
}


.mca-display {
  display:flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
}

.mca-display .card-title.h5 {
  margin-bottom: 20px;
}

.mca-display .card {
  display: flex;
  flex-direction: row;
}

.mca-display .mca-button {
  padding: 16px;
}

.mca-display .mca-button button {
  font-size: 20px;
}

.mca-display .card {
  display: flex;
  flex-direction: column;
}

.mca-display .operation {
  display: flex;
  flex-wrap: wrap;
}

.chart-display {
  display: flex;
  flex-direction: column;
  margin-bottom: 40px;
}

.chart-chart {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  
}

.chart-button {
  text-align: center;
}


.inputZone {
  background-color: rgba(152, 152, 152, 0.2);
  border: solid;
  border-color: darkgrey;
}

.singleDisplay {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px;
  font-size: 25px;
}

.progressSection {

  justify-content: center;
  align-items: center;
  padding: 20px;
}

/*--------------------------------------------------------------- Mobile Responsive ---------------------------------*/

@media (max-width: 1000px) {
  .Task {
      padding-top: 60px;
      margin-left: 0;
  }

  .recharts-surface {
    width: 100%;
    height: 100%;
  }

}
* {
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    /* ===== Colors ===== */
    --body-color: #E4E9F7;
    --sidebar-color: #FFF;
    --primary-color: #695CFE;
    --primary-color-light: #F6F5FF;
    --toggle-color: #DDD;
    --text-color: #707070;
}

body {
    height: 100vh;
    background: #E4E9F7;
    background: var(--body-color);
}
